@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
}

a {
  text-decoration: none;
}

nav ul {
  list-style-type: none;
}

img {
  display: block;
  width: 100%;
  object-position: center;
  object-fit: cover;
}

button {
  display: block;
  cursor: pointer;
  background-color: transparent;
  border: 0;
}

header {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.875rem 1.5625rem;
}

.header-logo {
  width: 9.375rem;
  cursor: pointer;
}

.header-nav {
  position: absolute;
  left: 1.5625rem;
  right: 1.5625rem;
  top: 9.375rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  background: white;
  text-align: center;
  padding: 2.5rem 0rem;
  transition: 0.5s ease;
  transform: translate(0, -31.25rem);
  z-index: 2;
}
.header-nav.display {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}
.header-nav::before {
  content: "";
  position: absolute;
  top: -2.5rem;
  right: 0;
  border-left: 2.5rem solid transparent;
  border-bottom: 2.5rem solid white;
}
.header-nav ul {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
.header-nav ul a {
  font-size: 1.40625rem;
  color: #818498;
}
.header-nav ul li:last-child {
  font-family: "Fraunces", serif;
  padding: 1.25rem 1.875rem;
  border-radius: 3.125rem;
  background-color: #fad400;
  cursor: pointer;
}
.header-nav ul li:last-child:hover {
  background-color: #6eceff;
}
.header-nav ul li:last-child:hover a {
  color: white;
}
.header-nav ul li:last-child a {
  color: #23303e;
  text-transform: uppercase;
}

.menuBtn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.125rem;
  height: 5rem;
  cursor: pointer;
  transform: scale(0.8);
  transition: all 0.5s ease-in-out;
}
.menuBtn.open .menuBtn__burger {
  transform: translateX(-3.125rem);
  background-color: transparent;
  box-shadow: none;
}
.menuBtn.open .menuBtn__burger::before {
  transform: rotate(45deg) translate(2.1875rem, -2.1875rem);
}
.menuBtn.open .menuBtn__burger::after {
  transform: rotate(-45deg) translate(2.1875rem, 2.1875rem);
}

.menuBtn__burger {
  width: 3.125rem;
  height: 0.375rem;
  background-color: white;
  border-radius: 0.3125rem;
  box-shadow: 0 0.125rem 0.3125rem rgba(255, 101, 47, 0.2);
  transition: all 0.5s ease-in-out;
}
.menuBtn__burger::before, .menuBtn__burger::after {
  content: "";
  position: absolute;
  width: 3.125rem;
  height: 0.375rem;
  background-color: white;
  border-radius: 0.3125rem;
  box-shadow: 0 0.125rem 0.3125rem rgba(255, 101, 47, 0.2);
  transition: all 0.5s ease-in-out;
}
.menuBtn__burger::before {
  transform: translateY(-1rem);
}
.menuBtn__burger::after {
  transform: translateY(1rem);
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-image: url("/images/mobile/image-header.jpg");
  background-position: center;
  background-size: cover;
}
main h1 {
  font-family: "Fraunces", serif;
  color: white;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.46875rem;
  word-break: break-word;
  margin: 0 0.625rem;
  margin-bottom: 3.75rem;
  font-weight: 900;
  font-size: 2.5rem;
}

.arrow-down {
  height: 9.375rem;
  width: auto;
}

.section--articles {
  display: grid;
  justify-items: center;
  align-items: center;
}
.section--articles article {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  line-height: 1.875rem;
  width: 100%;
  max-width: 28.125rem;
  padding: 3.75rem 1.25rem;
  gap: 1.875rem;
}
.section--articles article h2 {
  font-family: "Fraunces", serif;
  line-height: 2.8125rem;
}
.section--articles > article {
  max-width: 31.25rem;
}
.section--articles > article h2 {
  font-size: 2.1875rem;
  color: #23303e;
}
.section--articles > article p {
  color: #818498;
}
.section--articles > article button {
  position: relative;
  font-family: "Fraunces", serif;
  font-size: 1.0625rem;
  text-transform: uppercase;
}
.section--articles > article button::after {
  content: "";
  position: absolute;
  bottom: -0.21875rem;
  left: -0.46875rem;
  right: -0.46875rem;
  height: 0.625rem;
  border-radius: 0.4375rem;
  z-index: -1;
}
.section--articles > div {
  position: relative;
}
.section--articles > div:nth-of-type(1) {
  width: 100%;
  color: #25564b;
}
.section--articles > div:nth-of-type(2) {
  width: 100%;
  color: #19536b;
}
.section--articles > div > article {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

.btn--yellow::after {
  background-color: rgba(250, 212, 0, 0.25);
}

.btn--yellow:hover::after {
  background-color: #fad400;
}

.btn--pink::after {
  background-color: rgba(254, 120, 103, 0.25);
}

.btn--pink:hover::after {
  background-color: #fe7867;
}

.section--testimonials {
  padding: 3.75rem 1.875rem;
}
.section--testimonials > h3 {
  font-family: "Fraunces", serif;
  color: #a7abae;
  letter-spacing: 0.3125rem;
  font-size: 1.125rem;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 3.5rem;
}

.testimonials {
  display: grid;
  gap: 4rem;
}

.testimonial {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.testimonial img,
.testimonial p {
  margin-bottom: 2.5rem;
}
.testimonial p {
  text-align: center;
  color: #5a636c;
  line-height: 2.1875rem;
  width: 100%;
  max-width: 21.875rem;
}
.testimonial h3 {
  font-family: "Fraunces", serif;
  color: #23303e;
  margin-bottom: 0.9375rem;
}
.testimonial h4 {
  color: #a7abae;
  font-size: 1rem;
}

.profileImg {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 50%;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3.75rem 1.875rem 1.875rem 1.875rem;
  background-color: #90d4c5;
}
footer nav {
  max-width: 23.4375rem;
  width: 100%;
}
footer nav ul {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
}
footer nav ul li a {
  color: #25564b;
}
footer nav ul li a:hover {
  color: white;
}

.footer-logo {
  width: 10.9375rem;
  margin-bottom: 2.5rem;
  cursor: pointer;
  filter: invert(28%) sepia(5%) saturate(4119%) hue-rotate(116deg) brightness(30%) contrast(85%);
}

.socials {
  display: flex;
  margin-top: 5rem;
  gap: 1.875rem;
}

.social {
  width: auto;
  filter: brightness(50%);
}
.social:hover {
  filter: brightness(0) invert(1);
}

@media (min-width: 600px) {
  main {
    background-image: url("/images/desktop/image-header.jpg");
  }
}
@media (min-width: 750px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }
}
@media (min-width: 800px) {
  .menuBtn {
    display: none;
  }

  .header-nav {
    position: static;
    background: transparent;
    padding: 0rem;
    opacity: 1;
    visibility: visible;
    transform: translate(0, 0);
    z-index: 2;
  }
  .header-nav::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
  }
  .header-nav ul {
    flex-direction: row;
    align-items: center;
    gap: 2.5rem;
  }
  .header-nav ul a {
    font-size: 1.40625rem;
    color: white;
  }
  .header-nav ul li:last-child {
    font-family: "Fraunces", serif;
    padding: 1.25rem 1.875rem;
    border-radius: 3.125rem;
    background-color: white;
  }
  .header-nav ul li:last-child a {
    color: #23303e;
    text-transform: uppercase;
  }

  .section--articles {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  .section--articles article {
    padding: 0.625rem 1.25rem;
  }
  .section--articles > article {
    text-align: left;
    align-items: flex-start;
  }
  .section--articles > article:nth-of-type(1) {
    order: 1;
  }
  .section--articles > article:nth-of-type(2) {
    order: 4;
  }
  .section--articles > div:nth-of-type(1) {
    order: 5;
  }
  .section--articles > div:nth-of-type(2) {
    order: 6;
  }

  .img--egg {
    order: 2;
  }

  .img--pinkCup {
    order: 3;
  }
}
@media (min-width: 1000px) {
  .section--testimonials {
    padding: 7.5rem 1.875rem;
  }

  .testimonials {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .section--articles article {
    padding: 0rem 1.25rem 2.5rem 1.25rem;
  }
}